import React from 'react';
import { Field } from 'react-final-form';
import { isApple } from '../lib/utils';



const Select = ({name, children }) => {
  
    return (
        <Field defaultValue="default" name={name} component="select" className="select">
            {children}

        </Field>
    )
}

export default Select;

