import React from 'react';
const Button = (props) => {
	const {
		classList,
		handleClick = function () { return null },
		children,
		url,
		as,
		...rest
	 } = props;
	if (url) {
		return (
			// <Link href={url} as={as || url}>
				<a className={`button ${classList}`} {...rest}>
					{children}				
				</a>
			// </Link>
		)
	}
	else { 
		return (
			<button
				className={`button ${classList}`}
				onClick={() => { handleClick() }}
				{...rest}
				type={ props.type ? props.type : "button" }
			>
				{children}	
			</button>
		)
	}
}

export default Button;
