

export const formatNumber = (value) => {
    if (value) {
        let result = value.replace(/ /g, "");

        if(result.length >= 10){
            result = result.slice(0, -1).replace(/(\d{3})(\d{3})(\d{1})/, "$1 $2 $3")
            return result;
        }

        if (result.length >= 7) {
            result = result.replace(/(\d{3})(\d{3})(\d{1})/, "$1 $2 $3")
            return result;
        }

        if (result.length >= 3) {
            result = result.replace(/(\d{3})(\d{1})/, "$1 $2")
            return result;
        }


        return value;

    }

    return "";
}

export const formatCode = (value) => {
    if (value) {
        if(value.length <= 3)
            return value;
        else{
            let result = value.slice(0, -1);
            console.log("tu", result)
            return result;
        }

    }

    return "+"

}