import React from "react"
import back from "../../static/images/back.svg";
import more from "../../static/images/more.svg";


const menu = [
  "Start",
  "Numer telefonu komórkowego",
  "Kod weryfikacyjny",
  "Twoje dane",
  "Twoje dane adresowe",
  "Nazwisko panieńskie matki",
  "Twój adres e-mail",
  "Twoje dane majątkowe",
  "Oświadczenia",
  "Gratulacje!",
]

const Header = ({ handlePreviousStep, percent, state, setState }) => {
  return (
    <>
      <header className="header-left">

        <div className="header-menu_container">
          <nav>
            <ul>
              {menu.map((element, index) => {
                if(index === 0) return null;
                if(index < state)
                  return(
                    <li key={index} onClick={() => setState(index)} className="menu_item-completed"> {element} </li>
                  )
                if(index === state)
                    return(
                      <li key={index} className="menu_item-active"> {element} </li>
                    )
                if(index > state)
                    return(
                      <li key={index} className="menu_item-disabled"> {element} </li>
                    )
              })}
            </ul>
          </nav>
        </div>
        <div className="header-progress_container">
          <progress
            className="progress is-primary"
            value={percent}
            max={100}
          >
            {percent + "%"}
          </progress>
        </div>
      </header>
      <header className="header-top">
        <div className="header-buttons_container">    
          <button name="button_back" 
            onClick={() => handlePreviousStep()}
          >
            <img
              src={back}
            />
            <p> Back </p>
          </button>
          <h1 className="header_title"> Wniosek  <input id="hack" inputMode="tel" /> </h1>

          <button 
            // onClick={() => window.location.reload()}
          >
            <img className="header_hamburger" src={more} /> 
          </button>
        </div>
        

        <div className="header-progress_container">
          <progress
            className="progress is-primary"
            value={percent}
            max={100}
          >
            {percent + "%"}
          </progress>
        </div>
      </header>
    </>
  )
}

export default Header
