import React, { createContext, useContext, useReducer } from 'react';

const GlobalState = createContext();

const GlobalStateProvider = ({ reducer, initialState, children }) => {
    return (
        <GlobalState.Provider value={useReducer(reducer, initialState)}>
            {children}
        </GlobalState.Provider>
    )
}

//used to be: const getGlobalState = () => useContext(GlobalState);
const GetGlobalState = () => {
    const [state, dispatch] = useContext(GlobalState);

    const customDispatch = (action, data = {}) => {
        if (!action) {
            console.error("Provide action object or string");
            return false;
        }
        else if (typeof action === 'object') { //support classic usage
            dispatch(action)
        }
        else if (typeof action === 'string') {
            dispatch({
                type: action,
                ...data
            })
        }
    }

    return [
        state,
        customDispatch
    ]
}

export { GlobalState, GlobalStateProvider, GetGlobalState }
